import React from 'react';
import { ButtonBase } from '@mui/material';
import { CardImg2 } from './Img';
import { cardNoMap } from '../constants/cards';
import { darken } from '@mui/material/styles';
import CheckIcon from '@mui/icons-material/Check';
import { CountDisplay } from '../cards/components/CountDisplay';


export const OperableCard = ({ cardNo, handleClick, handleLongPress, handleHoverOn, handleHoverOff, cardSize, count, editable, isCountDisplay }) => {

  // console.log(isCountDisplay)


  if (!editable) {
    return (
      <DisplayedCard
        cardNo={cardNo}
        cardSize={cardSize}
        count={count}
        isCountDisplay={isCountDisplay}
      />
    )
  }


  let timerID;

  const handleMouseDown = () => {
    timerID = setTimeout(() => {
      handleLongPress();
    }, 300); // 300ms = 0.3s
  };

  const handleMouseUp = () => {
    clearTimeout(timerID);
  };

  const handleTouchMove = () => {
    clearTimeout(timerID);
  };

  return (
    <ButtonBase
      onMouseEnter={handleHoverOn}
      onMouseLeave={handleHoverOff}
      onClick={handleClick}
      onMouseDown={handleMouseDown}
      onMouseUp={handleMouseUp}
      onTouchStart={handleMouseDown}
      onTouchEnd={handleMouseUp}
      onTouchMove={handleTouchMove}
      onContextMenu={(e) => {
        e.preventDefault();
      }}
    >
      <DisplayedCard
        cardNo={cardNo}
        cardSize={cardSize}
        count={count}
        isCountDisplay={isCountDisplay}
      />
    </ButtonBase>
  );
};


const DisplayedCard = ({ cardNo, cardSize, count, isCountDisplay }) => {

  return (<>
    <CardImg2 cardNo={cardNo} size={cardSize} fade={true} />
    <div
      style={{
        position: 'absolute',
        top: 0,
        right: 0,
      }}
    >
      <CountDisplay
        cardNo={cardNo}
        count={count}
        size={Math.max(Math.min(cardSize / 3, 36), 20)}
        isCountDisplay={isCountDisplay}
      />
    </div>
  </>)


}
