import React, { useEffect, useState } from 'react';
import { useRecoilState } from 'recoil';

import _ from 'lodash';

import { cardSetsState, cardSetState, atoms, isMobileState, cardSetEditDateState } from '../states/recoilStates';


import { Unstable_Grid2 as Grid, Modal } from '@mui/material';
import SaveIcon from '@mui/icons-material/Save';
import MenuIcon from '@mui/icons-material/Menu';
import FileDownloadIcon from '@mui/icons-material/FileDownload';





import { DeckNameEdit } from './CustomTextField';
import { CommonIconAndTextButton, RemoveButton, } from '../../components/CustomButton';
import { Deck } from './Deck';
import { CardsOfCardSet } from './CardsOfCardSet';








export const DeckEditing = ({ props }) => {
  const { viewWidth } = props

  const [cardSet, setCardSet] = useRecoilState(cardSetState);
  const [cardSets, setCardSets] = useRecoilState(cardSetsState);
  const [cardSetEditDate, setCardSetEditDate] = useRecoilState(cardSetEditDateState);

  const [isMobile, setIsMobile] = useRecoilState(isMobileState);



  // モーダルの状態を管理するためのstate
  const [open, setOpen] = useState(false);

  const findIndexWithSameDate = (newCardSets, targetDate) => {
    for (let i = 0; i < newCardSets.length; i++) {
      if (new Date(newCardSets[i].date).getTime() === targetDate) {
        return i; // 日時が一致した時点でそのインデックスを返す
      }
    }
    return -1; // 一致する日時がない場合は-1を返す
  };


  return (<>
    <Grid container spacing={0.5} sx={{ paddingX: 1, paddingTop: 0.5 }}>
      <Grid>
        <CommonIconAndTextButton
          hundleClick={() => {

            //編集中cardSetの日時
            const targetDate = cardSet.date

            //cardSetの更新
            const newCardSet = _.cloneDeep(cardSet)
            newCardSet.date = Date.now()
            setCardSet(newCardSet)

            setCardSetEditDate(newCardSet.date)
            localStorage.setItem('conantcg-cardSetEditDate', newCardSet.date);


            //cardSetsの更新
            const newCardSets = [...cardSets];


            const index = findIndexWithSameDate(newCardSets, targetDate);

            newCardSets.splice(index, 1); // 元のindexの要素を削除
            newCardSets.unshift(_.cloneDeep(newCardSet)); // 先頭に追加
            setCardSets(newCardSets);
            localStorage.setItem('conantcg-cardSets', JSON.stringify(newCardSets));
          }}
          startIcon={<SaveIcon />}
          bgColor={'#00ffff'}
        >
          保存
        </CommonIconAndTextButton>
      </Grid>
      <Grid>
        <CommonIconAndTextButton
          hundleClick={() => {
            setOpen(true); // ボタンをクリックしたときにモーダルを開く
          }}
          startIcon={<MenuIcon />}
          bgColor={'#ffffff'}
        >
          デッキ選択
        </CommonIconAndTextButton>
        <Modal
          open={open}
          onClose={() => setOpen(false)}
        >
          <div
            style={{
              width: isMobile ? '100%' : '50%',
              height: '100%',
              overflow: 'auto', // 中身が高さを超えた場合にスクロールできるようにする
              margin: '0 auto',
              backgroundColor: '#000000',

            }}
          >
            <Deck
              props={{ ...props, setOpen }}
            />
          </div>
        </Modal>


      </Grid>
      <Grid>
      <DownloadButtonCSV />
      </Grid>

      <Grid xs={12} >
        <DeckNameEdit
          value={cardSet.name || ''}
          onChange={(e) => {
            const newCardSet = _.cloneDeep(cardSet);
            newCardSet.name = e.target.value
            setCardSet(newCardSet);
          }}
        />
      </Grid>

    </Grid>

    <CardsOfCardSet
      cardSet={cardSet}
      setCardSet={setCardSet}
      viewWidth={viewWidth}
      isMobile={isMobile}
    />


  </>)

}




// データをCSV形式に変換する関数
function convertToCSV(objArray) {
  const array = typeof objArray !== 'object' ? JSON.parse(objArray) : objArray;
  let str = '';
  let headers = ['date', 'name', 'partner', 'case'];

  // 最も長いdeck配列の長さを取得
  let maxDeckLength = Math.max(...array.map(item => item.deck.length));

  // deckのヘッダーを作成
  for (let i = 0; i < maxDeckLength; i++) {
      headers.push(`deck${i}`);
  }

  // ヘッダー行を作成
  str += `${headers.join(",")}\r\n`;

  // データ行を作成
  for (let i = 0; i < array.length; i++) {
      let line = '';
      line += `"${array[i].date}",`;
      line += `"${array[i].name}",`;
      line += `"${array[i].partner}",`;
      line += `"${array[i].case}",`;

      // deckのデータを作成
      for (let j = 0; j < maxDeckLength; j++) {
          line += `"${array[i].deck[j] || ''}",`;
      }

      str += line + '\r\n';
  }

  return str;
}


// CSVをダウンロードする関数
function downloadCSV(array) {
  const link = document.createElement('a');
  let csv = convertToCSV(array);
  link.setAttribute('href', 'data:text/csv;charset=utf-8,' + encodeURIComponent(csv));
  link.setAttribute('download', 'data.csv');
  link.click();
}

// コンポーネント
const DownloadButtonCSV = () => {
  const storagedItem = localStorage.getItem('conantcg-cardSets');
  const data = storagedItem
    ? JSON.parse(storagedItem).map(item => ({ ...item, date: Number(item.date) || Date.now() }))
    : [{ name: 'デッキ名', partner: '', case: '', deck: [], date: Date.now() }];

  return (
    <CommonIconAndTextButton
    hundleClick={() => downloadCSV(data)}
    startIcon={<FileDownloadIcon />}
    bgColor={'#ffffff'}
  >
    全デッキをCSVでダウンロード
  </CommonIconAndTextButton>

  );
}













