export const Top = () => {
  const [value, setValue] = useState(0)

  const [actualHeight, setActualHeight] = useState(0);

  const [windowWidth, windowHeight] = useWindowSize()

  var urlBarHeight = windowHeight - document.documentElement.clientHeight;

  const tabRef = useRef(null);

  useEffect(() => {
    if (tabRef.current) {
      const height = tabRef.current.getBoundingClientRect().height;
      let newActualHeight = windowHeight - urlBarHeight - height;
      setActualHeight(newActualHeight);
    }
  }, [windowHeight, urlBarHeight]);


  // console.log(windowHeight,actualHeight)



  return (
    <Router basename="/conan-cardgame" >
      {/* <HideOnScrollAppBar> */}
      <Grid container ref={tabRef}>
        <Grid>
          <Tabs value={value} onChange={(event, newValue) => setValue(newValue)}>
            <Tab label="デッキ構築" component={Link} to="/cardlist" />
            {/* <Tab label="シミュレータ" component={Link} to="/simulator" /> */}
          </Tabs>
        </Grid>
      </Grid>
      {/* </HideOnScrollAppBar> */}
      <Routes>
        <Route path="/" element={actualHeight > 0 ? <Card setTabValue={setValue} actualHeight={actualHeight} /> : null} />
        <Route path="/cardlist" element={actualHeight > 0 ? <Card setTabValue={setValue} actualHeight={actualHeight} /> : null} />
        {/* <Route path="/simulator" element={<Simulator setTabValue={setValue} />} /> */}
      </Routes>
      {windowWidth < windowHeight &&
        <Footer />
      }
    </Router>
  );
};