import React, { useState, useEffect } from 'react';
import { useRecoilState } from 'recoil';
import { Button, Modal, } from '@mui/material';
import { Alert, Unstable_Grid2 as Grid, Snackbar } from '@mui/material';
import { darken, lighten } from '@mui/system';
import ErrorOutlineIcon from '@mui/icons-material/ErrorOutline'


import { cardSetState, isMobileState } from '../states/recoilStates';


import { cardNoMap } from '../../constants/cards';


import { CardImg2 } from '../../components/Img';
import { CloseButton, NextButton, PrevButton, AddButton, RemoveButton } from '../../components/CustomButton';
import { updateCardSetWithNewCard } from '../utils/updateCardSet';
import { CustomSnackBar } from '../../components/CustomSnackBar';
import { CountDisplay, calculateCount } from './CountDisplay';
import { removeAndSetCardSet } from '../utils/updateCardSet';


export const CardDetails = ({ cardNoList, open, selectedCardNo, setSelectedCardNo, handleClose }) => {
  const [cardSet, setCardSet] = useRecoilState(cardSetState);

  //SnackBar
  const [message, setMessage] = useState('');
  const [openSnackbar, setOpenSnackbar] = useState(false);
  const showMessage = (msg) => {
    setMessage(msg);
    setOpenSnackbar(true);
  };
  const handleCloseSnackBar = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpenSnackbar(false);
  };



  const [dimensions, setDimensions] = useState({ width: window.innerWidth, height: window.innerHeight });

  const size = 1000

  // console.log(selectedCard,cardList)

  useEffect(() => {
    const handleResize = () => {
      setDimensions({ width: window.innerWidth, height: window.innerHeight });
    };
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const handleNext = () => {
    const currentIndex = cardNoList.findIndex(cardNo => cardNo === selectedCardNo);
    const nextIndex = (currentIndex + 1) % cardNoList.length;
    setSelectedCardNo(cardNoList[nextIndex]);
  };

  const handlePrev = () => {
    const currentIndex = cardNoList.findIndex(cardNo => cardNo === selectedCardNo);
    const prevIndex = (currentIndex - 1 + cardNoList.length) % cardNoList.length;
    setSelectedCardNo(cardNoList[prevIndex]);
  };

  const modalWidth = Math.min(dimensions.width, 1250);
  const modalHeight = Math.min(dimensions.height, 1250);

  const maxWidth = cardNoMap[selectedCardNo]?.type === 'case' ? size : size / 1.4;
  const maxHeight = (cardNoMap[selectedCardNo]?.type === 'case' ? size / 1.4 : size);

  const w = Math.min(dimensions.width / maxWidth, 1);
  const h = Math.min(dimensions.height / maxHeight, 1);

  const wh = Math.min(w, h);

  let viewSize = size * wh * 0.96;
  if ((viewSize + 100) > dimensions.height) {
    viewSize = dimensions.height - 100
  }

  const myRef = React.useRef(null);

  useEffect(() => {
    if (myRef.current) {
      myRef.current.scrollTop = 0;
    }
  }, [selectedCardNo]);


  return (
    <Modal
      open={open}
      onClose={handleClose}
      aria-labelledby="simple-modal-title"
      aria-describedby="simple-modal-description"
    >
      <div
        style={{
          width: modalWidth,
          height: modalHeight,
          display: 'flex', alignItems: 'center', justifyContent: 'center',
          position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)',
        }}
        onClick={handleClose}  // 全体クリックで閉じる
      >
        <div
          style={{
            width: cardNoMap[selectedCardNo].type === 'case' ? viewSize : viewSize / 1.4,
            height: cardNoMap[selectedCardNo].type === 'case' ? viewSize / 1.4 : viewSize,
            display: 'flex', alignItems: 'center', justifyContent: 'center',
            position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)'
          }}
          onClick={(event) => event.stopPropagation()}  // カードクリックでは閉じるを実行させない
        >

          {cardNoMap[selectedCardNo] && <div
            ref={myRef}
            style={{
              width: cardNoMap[selectedCardNo].type === 'case' ? viewSize : viewSize / 1.4,
              height: cardNoMap[selectedCardNo].type === 'case' ? viewSize / 1.4 : viewSize,
              overflow: 'auto',
            }}>
            <CardImg2 cardNo={selectedCardNo} size={viewSize} boxShadow={'0px 0px 5px 5px rgba(0,0,0,0.5)'}
            />
          </div>}

          <CloseButton
            sx={{ position: 'absolute', right: 0, top: -40 }}
            handleClick={(event) => { event.stopPropagation(); handleClose(); }}
          />

          <div style={{
            position: 'absolute', bottom: -40, display: 'flex', justifyContent: 'space-between', width: '100%',
          }}>
            <div>
              {cardNoList.length >= 2 &&
                <PrevButton handleClick={(event) => { event.stopPropagation(); handlePrev(); }} />
              }
            </div>
            <div style={{ display: 'flex', gap: 4 }}>
              <RemoveButton
                handleClick={(e) => {
                  e.stopPropagation();
                  removeAndSetCardSet(setCardSet, selectedCardNo)
                }} />
              <CountDisplay
                cardNo={selectedCardNo}
                count={calculateCount({ cardNo: selectedCardNo, cardSet })}
                size={36}
              />
              <AddButton
                handleClick={(e) => {
                  e.stopPropagation();
                  updateCardSetWithNewCard({
                    cardNo: selectedCardNo,
                    cardSet,
                    setCardSet,
                    showMessage,
                  })
                }}
              />
            </div>
            <div>
              {cardNoList.length >= 2 &&
                <NextButton handleClick={(event) => { event.stopPropagation(); handleNext(); }} />
              }
            </div>
          </div>




        </div>
        <CustomSnackBar
          openSnackbar={openSnackbar}
          handleClose={handleCloseSnackBar}
          message={message}
        />
      </div>

    </Modal>
  );

};

