import React, { useEffect } from 'react';
import { atom, useRecoilValueLoadable, useRecoilState } from 'recoil';
import _ from 'lodash';


export const cardSetsState = atom({
  key: 'cardSetsState',
  default: (() => {
    try {
      // 昔のキーが残っているか確認する
      const oldKey = 'conan-cardgame-deckList';
      const oldItem = localStorage.getItem(oldKey);
      if (oldItem) {
        // 昔のキーが残っていた場合、その内容を新しいキーと内容に変換、ローカルストレージに保存する
        const oldCardSets = JSON.parse(oldItem)
        const newCardSets = []


        oldCardSets.forEach((oldCardSet, index) => {

          const newCardSet = { deck: [], date: Date.now() } // dateを初期化
          Object.keys(oldCardSet).forEach(key => {
            if (key === 'partner') {
              newCardSet.partner = oldCardSet[key]?.number || ''
            } else if (key === 'case') {
              newCardSet.case = oldCardSet[key]?.number || ''
            } else if (key === 'deckName') {
              newCardSet.name = oldCardSet[key] || ''
            } else if (key === 'deck') {
              oldCardSet.deck.forEach(card => {
                newCardSet.deck.push(card.number)
              })
            }
          })
          newCardSets.push(newCardSet);
        })


        localStorage.setItem('conantcg-cardSets', JSON.stringify(newCardSets));

        // 昔のキーのローカルストレージの内容を削除する
        localStorage.removeItem(oldKey);
        localStorage.removeItem('conan-cardgame-deckContents');
        localStorage.removeItem('conan-cardgame-selectedDeckIndex');
      }

      const storagedItem = localStorage.getItem('conantcg-cardSets');
      return storagedItem
        ? JSON.parse(storagedItem).map(item => ({ ...item, date: Number(item.date) || Date.now() }))
        : [{ name: 'デッキ名', partner: '', case: '', deck: [], date: Date.now() }];


    } catch (error) {
      console.error('Failed to parse card sets from localStorage:', error);
      return [{ name: 'デッキ名', partner: '', case: '', deck: [], date: Date.now() }]; // デフォルトの値を返す
    }
  })(),
});


export const cardSetState = atom({
  key: 'cardSetState',
  default: (() => {
    try {
      const storagedItem = JSON.parse(localStorage.getItem('conantcg-cardSets'));
      const targetDate = Number(localStorage.getItem('conantcg-cardSetEditDate'));

      if (storagedItem) {

        // 配列を走査し、初めてdateがtargetDateと一致する要素を見つける
        for (let i = 0; i < storagedItem.length; i++) {
          if (Number(storagedItem[i].date) === targetDate) {
            return _.cloneDeep(storagedItem[i]); // ディープコピーして返す
          }
        }

        // 一致する要素がない場合、デフォルトのオブジェクトを返す
        localStorage.setItem('conantcg-cardSetEditDate', storagedItem[0].date)
        return _.cloneDeep(storagedItem[0]);



      }

      const date = Date.now()
      localStorage.setItem('conantcg-cardSetEditDate', date)
      return { name: 'デッキ名', partner: '', case: '', deck: [], date };

    } catch (error) {
      console.error('Failed to parse card sets from localStorage:', error);
      const date = Date.now()
      localStorage.setItem('conantcg-cardSetEditDate', date)
      return { name: 'デッキ名', partner: '', case: '', deck: [], date };
    }
  })(),
});


export const cardSetEditDateState = atom({
  key: 'cardSetEditDateState',
  default: 0,
});

export const hoveredCardNoAndSideState = atom({
  key: 'hoveredCardNoAndSideState', // unique ID (with respect to other atoms/selectors)
  default: ['', ''], // default value (aka initial value)
});

export const isMobileState = atom({
  key: 'isMobileState', // unique ID (with respect to other atoms/selectors)
  default: true, // default value (aka initial value)
});







export const initialState = {
  sortField: 'number',
  sortOrder: 'asc',
  filterText: '',
  filterProduct: '',
  filterColor: [],
  filterType: [],
  filterRarity: [],
  filterParallel: ['(無印)'],
};

const atoms = Object.fromEntries(
  Object.keys(initialState).map(key => [
    key,
    atom({
      key,
      default: initialState[key],
    }),
  ])
);

export { atoms };
