import React, { useRef } from 'react';
import { useRecoilState } from 'recoil';

import _ from 'lodash';

import { cardSetsState, cardSetState, atoms, hoveredCardNoAndSideState } from '../states/recoilStates';


import { cardNoMap } from '../../constants/cards';
import { OperableCard } from '../../components/OperableCard';






export const CardsPerLevel = ({ cardNos, viewWidth, handleClick, setSelectedCardNo, setOpen, isMobile, editable }) => {

  const [hoveredCardNoAndSide, setHoveredCardNoAndSide] = useRecoilState(hoveredCardNoAndSideState);



  const columnWidth = Math.min((viewWidth - 16) / 8);//=50
  const cardSize = Math.min((viewWidth - 16) / 8 * 1.4);//=70

  // レベルごとにカードを分類
  const levelSortedDeck = [...Array(8)].map(() => []);
  cardNos.forEach(cardNo => {
    const card = cardNoMap[cardNo]
    const level = card.level;
    levelSortedDeck[level - 1].push(cardNo);
  });


  const divRef = useRef(null);

  // useEffect(() => {
  //   if (divRef.current) {
  //     console.log('div width:', divRef.current.offsetWidth);
  //   }
  // }, []);


  // console.log(columnWidth, cardSize)

  if( cardNos.length === 0 ) return <></>;


  return (
    <div style={{ margin: 8, marginTop: 4 }} >

      <div style={{ width: '100%', display: 'flex', flexDirection: 'row' }}>
        {levelSortedDeck.map((cardNosPerLevel, levelIndex) => (
          <div key={levelIndex}
            style={{
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              width: columnWidth,

            }}>
            <div style={{
              backgroundColor: 'white',
              borderRadius: 10,
              color: 'black',
              textAlign: 'center',
              width: columnWidth,
              height: 20,
              lineHeight: '20px',
              fontWeight: 'bold',
              fontSize: 16,
            }}>{levelIndex + 1}</div>

            {cardNosPerLevel.reduce((acc, cardNo) => {
              const existingCard = acc.find(item => item.number === cardNo);
              if (existingCard) {
                existingCard.count += 1;
              } else {
                acc.push({ number: cardNo, count: 1 });
              }
              return acc;
            }, []).sort((a, b) => {
              const cardA = cardNoMap[a.number]
              const cardB = cardNoMap[b.number]
              // カードタイプが 'character' のものを先に表示
              if (cardA.type === 'character' && cardB.type !== 'character') {
                return -1;
              } else if (cardA.type !== 'character' && cardB.type === 'character') {
                return 1;
              } else {
                return 0;
              }
            }).map((item, index) => {
              return (
                <div key={item.number + index} style={{ position: 'relative' }}
                >
                  <OperableCard
                    cardNo={item.number}
                    cardSize={cardSize}
                    handleClick={() => handleClick(item.number)}
                    handleHoverOn={isMobile ? undefined : () => setHoveredCardNoAndSide([item.number, 'right'])}
                    handleHoverOff={isMobile ? undefined : () => setHoveredCardNoAndSide(['', ''])}
                    handleLongPress={() => {
                      setSelectedCardNo(item.number)
                      setOpen(true)
                    }}
                    count={item.count}
                    editable={editable}
                  />
                </div>
              )
            })}
          </div>
        ))}

      </div>
      {/* <div style={{ width: '100%', position: 'relative', height: '40px', marginTop: 1, marginBottom: 1 }}>
      </div> */}
    </div>
  );
}
