import cardsByIdJson from '../data/cardsById.json';
import cardsByNumberJson from '../data/cardsByNumber.json';
import productsJson from '../data/products.json';
import cardsByNoJson from '../data/cardsByNo.json';


export const cardNoList = []
export const cardNoMap = {}
export const preCardSets = {}

cardsByNoJson.forEach(jsonEl => {
  cardNoList.push(jsonEl['No.'])
  cardNoMap[jsonEl['No.']] = {
    colors: jsonEl.colors.split('|'),
    abilities: jsonEl.abilities.split('|'),
    number: jsonEl['No.'],
    rarity: jsonEl.rarity,
    product: jsonEl.product,
    pieces: jsonEl.pieces === '' ? null : Number(jsonEl.pieces),
    id: jsonEl['ID'],
    name: jsonEl.name,
    type: jsonEl.type,
    tags: jsonEl.tags,
    level: jsonEl['Lv.'] === '' ? null : Number(jsonEl['Lv.']),
    actionPoint: jsonEl['AP'] === '' ? null : Number(jsonEl['AP']),
    logicPoint: jsonEl['LP'] === '' ? null : Number(jsonEl['LP']),
    cutIn: jsonEl.cutIn,
    inspiration: jsonEl.inspiration,
    note: jsonEl.note,
  }
    
  if (jsonEl.pieces) {
    if (!preCardSets[jsonEl.product]) {
      preCardSets[jsonEl.product] = {
        name: jsonEl.product,
        partner: '',
        case: '',
        deck: [],
      };
    }
    if (jsonEl.type === 'パートナー') {
      preCardSets[jsonEl.product].partner = jsonEl['No.'];
    } else if (jsonEl.type === '事件') {
      preCardSets[jsonEl.product].case = jsonEl['No.'];
    } else {
      // 文字列を数値に変換
      let pieces = parseInt(jsonEl.pieces, 10);
      // 数値分だけpush
      for (let i = 0; i < pieces; i++) {
        preCardSets[jsonEl.product].deck.push(jsonEl['No.']);
      }
    }
  }
  



})





export const cardData = {}
export const cardList = []


export const productData = (() => {
  const data = {};
  productsJson.forEach(jsonEl => {
    data[jsonEl.商品] = jsonEl;
  });
  return data;
})();

export const preDeckData = {}

productsJson.forEach(jsonEl => {
  if (jsonEl['Case'].includes('Deck')) {
    preDeckData[jsonEl.商品] = { name: jsonEl.商品, partner: '', case: '', deck: [] }
  }
})


export const initialDeckContents = { name: 'デッキ名', partner: '', case: '', deck: [] }








const textMap = {
  パートナー: 'partner',
  キャラ: 'character',
  イベント: 'event',
  事件: 'case',
}

cardsByIdJson.forEach(jsonEl => {

  cardData[jsonEl.ID] = {
    id: jsonEl.ID,
    name: jsonEl.カード名,
    type: textMap[jsonEl.種類],
    colors: ['色1', '色2'].map(key => jsonEl[key]).filter(Boolean),
    tags: jsonEl.特徴.split('_'),
    level: jsonEl.レベル,
    actionPoint: jsonEl.AP,
    logicPoint: jsonEl.LP,
    abilities: ['能力1', '能力2', '能力3'].map(ability => jsonEl[ability]).filter(Boolean),
    cutIn: jsonEl.カットイン,
    inspiration: jsonEl.ヒラメキ,
    numbers: [],
  }
})

cardsByNumberJson.forEach(jsonEl => {

  if (jsonEl.レア === 'SEC') return;

  cardList.push({
    number: jsonEl['NO.'],
    rarity: jsonEl.レア,
    product: jsonEl.商品,
    ...cardData[jsonEl.ID],
  })

  if (jsonEl.パラレル) {
    cardList.push({
      number: jsonEl['NO.'] + 'P',
      rarity: jsonEl.レア + 'P',
      product: jsonEl.商品,
      ...cardData[jsonEl.ID],
    })
  }

  if (cardData[jsonEl.ID]?.numbers) {
    cardData[jsonEl.ID].numbers.push(jsonEl['NO.'])
  }



  if (jsonEl.封入数) {
    const card = cardData[jsonEl.ID]
    if (card.type === 'partner' || card.type === 'case') {
      preDeckData[jsonEl.商品][card.type] = { number: jsonEl['NO.'], id: card.id, type: card.type }
    } else {
      for (let i = 0; i < Number(jsonEl.封入数); i++) {
        preDeckData[jsonEl.商品].deck.push({ number: jsonEl['NO.'], id: card.id, type: card.type });
      }
    }
  }
})








