import _ from 'lodash';
import { cardNoList, cardNoMap, cardList, productData } from '../../constants/cards';
import { isPartnerOrCase3, isPartnerOrCase2 } from '../../utils/commonUtils';

const textToKey = {
  'パートナー': 'partner',
  '事件': 'case',
}




export const updateCardSetWithNewCard = ({cardNo, cardSet, setCardSet, showMessage}) => {
  const card = cardNoMap[cardNo]

  if (card.type === 'パートナー') {
    setCardSet(prevCardSet => {
      return { ...prevCardSet, partner: cardNo };
    });
  } else if (card.type === '事件') {
    setCardSet(prevCardSet => {
      return { ...prevCardSet, case: cardNo };
    });

  } else {
    const quantityIncludedInDeck = cardSet.deck.filter(c => c === cardNo).length
    if (quantityIncludedInDeck >= 3) {
      showMessage('同じカードは3枚までです');
    } else {
      const newDeck = _.cloneDeep(cardSet.deck)
      const sameIdCards = newDeck.filter(c => cardNoMap[c].id === cardNoMap[cardNo].id);
      if (sameIdCards.length >= 3) {

        const sameIdDifferentNos = newDeck.filter(c => {
          return (
            cardNoMap[c].id === cardNoMap[cardNo].id && cardNoMap[c].number !== cardNo
          )
        })

        const indexToRemove = newDeck.indexOf(sameIdDifferentNos[0]);
        if (indexToRemove !== -1) {
          newDeck.splice(indexToRemove, 1);
        }
        showMessage('同じIDで異なるNo.のカードと入れ替えしました');
      }
      newDeck.push(cardNo);
      // newDeck.sort();
      setCardSet(prevCardSet => {
        return { ...prevCardSet, deck: newDeck };
      });
    }
  }
}


export const removeAndSetCardSet = (setCardSet, cardNo) => {

  const handleRemove = (prevState, currentCardNo) => {
    const newCardSet = _.cloneDeep(prevState);
    const deck = newCardSet.deck;
    const currentCard = cardNoMap[currentCardNo];
  
  
    if (isPartnerOrCase2(currentCard.type)) {
      newCardSet[textToKey[currentCard.type]] = '';
    } else {
      const index = deck.findIndex(deckCardNo => deckCardNo === currentCard.number);
      if (index !== -1) {
        deck.splice(index, 1);
      }
    }
    return newCardSet;
  };
  


  setCardSet(prevState => handleRemove(prevState, cardNo));
};





