export const DeckCount = ({ length }) => {
  if (length <= 40) return <></>;

  return <>
    <span style={{ color: 'red' }}>{length}</span>{'/40枚'}
  </>;
};




export const DeckActions = ({ index, handleAddDeck, initialDeckContents }) => {

  const [cardSet, setCardSet] = useRecoilState(cardSetState);
  const [cardSets, setCardSets] = useRecoilState(cardSetsState);
  const [cardSetEditDate, setCardSetEditDate] = useRecoilState(cardSetEditDateState);




  return (
    <div style={{ display: 'flex', justifyContent: 'flex-end', gap: 4, marginBottom: 4 }}>
      <div style={{ width: 'auto' }}>
        <CommonButton
          onClick={() => {
            const copiedDeck = _.cloneDeep(cardSets[index]);
            copiedDeck.name = `${copiedDeck.name}(コピー)`;
            handleAddDeck(copiedDeck);
          }}
        >
          <FileCopyIcon />コピーして新規作成
        </CommonButton>
      </div>

      <DeleteButton
        onClick={() => {
          if (window.confirm('削除しますか？')) {
            const newCardSets = [...cardSets];
            newCardSets.splice(index, 1);

            // デッキリストが空になる場合は新しいデッキを追加
            if (newCardSets.length === 0) {
              const newCardSet = _.cloneDeep(initialDeckContents);
              newCardSet.date = Date.now()
              newCardSets.push(newCardSet);

              //閉じられてもいいように設定しておく
              setCardSet(newCardSet)
              setCardSetEditDate(newCardSet.date)
              localStorage.setItem('conantcg-cardSetEditDate', newCardSet.date);


            }

            // newCardSets配列を走査し、newCardSets[i].date === cardSetEditDateが存在しない時
            const targetDateExists = newCardSets.some(item => item.date === cardSetEditDate);
            if (!targetDateExists) {
              setCardSet(newCardSets[0]);
              setCardSetEditDate(newCardSets[0].date);
              localStorage.setItem('conantcg-cardSetEditDate', newCardSets[0].date);
            }

            setCardSets(newCardSets);
            localStorage.setItem('conantcg-cardSets', JSON.stringify(newCardSets));

          }
        }}
        width={36}
        height={36}
      />
    </div>
  );
};




