import { atom, selectorFamily } from 'recoil';

const atoms = {};
const stateFamilies = {};

const recoilStates = {
  partner: [null, null],
  case: [null, null],
  deck: [[], []],
  file: [[], []],
  remove: [[], []],
  evidence: [[], []],
  hand: [[], []],
  field: [[], []],
  turn: [0, 0],
};

Object.keys(recoilStates).forEach((key) => {
  const recoilState = atom({
    key: `${key}RecoilState`,
    default: recoilStates[key],
  });

  atoms[key] = recoilState;

  const stateFamily = selectorFamily({
    key: `${key}StateFamily`,
    get: (faction) => ({ get }) => {
      const state = get(recoilState);
      return state[faction];
    },
    set: (faction) => ({ set }, newValue) => {
      set(recoilState, (prevState) => {
        const newState = [...prevState];
        newState[faction] = newValue;
        return newState;
      });
    },
  });

  stateFamilies[key] = stateFamily;
});



atoms.faction = atom({
  key: 'faction',
  default: 0, // 先攻を0、後攻を1とします
});
atoms.log = atom({
  key: 'log',
  default: [],
})
atoms.activationsPerTrigger = atom({
  key: 'activationsPerTrigger',
  default: {
    abilities: {
      'エンドフェイズ': [],
    },
    appliedEffects: {
      'エンドフェイズ': [],
    },
  },
})
atoms.stackedActivationQueues = atom({
  key: 'pendingActivations',
  default: [],
})







export { atoms, stateFamilies };





