
import React, { useEffect, useState, useMemo } from 'react';
import { useRecoilState } from 'recoil';
import { atoms, initialState } from '../states/recoilStates';



import { Checkbox, FormControlLabel, Unstable_Grid2 as Grid, IconButton, Typography } from '@mui/material';
import { darken } from '@mui/material/styles';



import { cardList, productData } from '../../constants/cards';

import { useWindowWidth } from '../../hooks/commonHooks';

import { SearchBox } from './CustomTextField';
import { HideOnScrollAppBar } from './CustomAppBar';
import { CommonToggleButtonGroup } from './CustomToggleButton';
import { CommonSelectMenu } from './CustomSelect';
import { CardOfList } from './CardOfList';
import { Deck } from './Deck';
import { EditButton, RefreshButton, SortOrderButton } from '../../components/CustomButton';
import { ExpndButton } from '../../components/CustomButton';





const Sorts = () => {
  const [sortField, setSortField] = useRecoilState(atoms.sortField);
  const [sortOrder, setSortOrder] = useRecoilState(atoms.sortOrder);

  return (
    <Grid container spacing={0.5}>
      <Grid>
        <SortOrderButton
          handleClick={() => {
            setSortOrder(prevSortOrder => prevSortOrder === 'asc' ? 'desc' : 'asc');
          }}
          sortOrder={sortOrder}
        />
      </Grid>
      <Grid>
        <CommonToggleButtonGroup
          exclusive={true}
          value={sortField}
          onChange={(e, newSortField) => {
            if (newSortField !== null) {
              setSortField(newSortField);
            }
          }}
          options={['number', 'id', 'level', 'actionPoint', 'logicPoint']}
        />
      </Grid>
    </Grid>
  );
};

const Filters = ({ filterEls }) => {
  return (
    <Grid container spacing={0.5}>
      {filterEls.map((filterEl, index) => {
        return (
          <Grid key={index} sx={{ display: 'flex', alignItems: 'center' }}>
            {filterEl.label !== 'パラレル' &&
              <div style={{ fontSize: 14, whiteSpace: 'nowrap' }}>
                {filterEl.label + '：'}
              </div>
            }
            <CommonToggleButtonGroup
              value={filterEl.value}
              onChange={filterEl.onChange}
              options={filterEl.options}
            />



          </Grid>
        )
      })}
    </Grid>
  );
};

export const SortAndFilter = ({ props }) => {

  const [sortField, setSortField] = useRecoilState(atoms.sortField);
  const [sortOrder, setSortOrder] = useRecoilState(atoms.sortOrder);
  const [filterText, setFilterText] = useRecoilState(atoms.filterText);
  const [filterProduct, setFilterProduct] = useRecoilState(atoms.filterProduct);
  const [filterColor, setFilterColor] = useRecoilState(atoms.filterColor);
  const [filterType, setFilterType] = useRecoilState(atoms.filterType);
  const [filterRarity, setFilterRarity] = useRecoilState(atoms.filterRarity);
  const [filterParallel, setFilterParallel] = useRecoilState(atoms.filterParallel);

  return (
    <Grid container sx={{ padding: 1, gap: 0.5, bgcolor:'#222' }}>




      <Grid sx={{ bgcolor: 'black', paddingX: 1, paddingY: 0.5 }} >
        <Typography sx={{ fontSize: '14px', lineHeight: 1, fontWeight: 'bold' }}>ソート</Typography>
        <Sorts />
      </Grid>


      <Grid sx={{ bgcolor: 'black', paddingX: 1, paddingY: 0.5 }} >
        <Typography sx={{ fontSize: '14px', lineHeight: 1.2, fontWeight: 'bold' }}>フィルター</Typography>

        <Grid container spacing={0.5}>
          <Grid sx={{ width: 400 }}>
            <SearchBox
              value={filterText}
              onChange={(e) => {
                setFilterText(e.target.value);
              }}
            />
          </Grid>

          {/* <Grid sx={{ width: 400 }}>
            <CommonSelectMenu
              label="商品"
              value={filterProduct}
              onChange={(event) => {
                setFilterProduct(event?.target.value || '');
              }}
              options={Object.keys(productData)}
            />
          </Grid> */}

          <Grid>
            <Filters
              filterEls={[
                {
                  label: '色',
                  value: filterColor,
                  onChange: (e, newFilterColor) => {
                    setFilterColor(newFilterColor);
                  },
                  options: ['青', '緑', '白', '赤', '黄', '黒'],
                },
                {
                  label: '種類',
                  value: filterType,
                  onChange: (e, newFilterType) => {
                    setFilterType(newFilterType);
                  },
                  options: ['パートナー', 'キャラ', 'イベント', '事件'],
                },
                {
                  label: 'レア',
                  value: filterRarity,
                  onChange: (event, newFilterRarity) => {
                    setFilterRarity(newFilterRarity);
                  },
                  options: ['C', 'R', 'SR', 'D', 'PR'],
                },
                {
                  label: 'パラレル',
                  value: filterParallel,
                  onChange: (event, newFilterParallel) => {
                    setFilterParallel(newFilterParallel);
                  },
                  options: ['(無印)', 'P(パラレル)'],
                },
              ]}
            />
          </Grid>

        </Grid>
      </Grid>

    </Grid>
  );
};
