import React from 'react';
import { ButtonBase } from '@mui/material';
import { CardImg2 } from '../../components/Img';
import { cardNoMap } from '../../constants/cards';
import { darken } from '@mui/material/styles';
import CheckIcon from '@mui/icons-material/Check';

const typeColors = {
  'キャラ': darken('#7f7fff', 0),
  'イベント': darken('#ff7f7f', 0.2),
  'パートナー': darken('#ffffff', 0.5),
  '事件': darken('#ffffff', 0.5),
}

const toRoman = {
  1: 'I',
  2: 'II',
  3: 'III',
};

export const calculateCount = ({cardNo, cardSet}) => {
  let count = 0;
  if (cardNoMap[cardNo].type === 'パートナー' && cardNo === cardSet.partner) {
    count = 1;
  } else if (cardNoMap[cardNo].type === '事件' && cardNo === cardSet.case) {
    count = 1;
  } else {
    count = cardSet.deck.filter(c => c === cardNo).length;
  }
  return count;
};





export const CountDisplay = ({ cardNo, count, size , isCountDisplay = true }) => {
  if (count <= 0 || !isCountDisplay ) {
    return <div style={{width: size, height: size}}/>;
  }



  return (
    <div style={{
      backgroundColor: typeColors[cardNoMap[cardNo].type],
      borderRadius: '50%',
      color: 'white',
      width: size,
      height: size,
      fontSize: size / 1.5,
      fontWeight: 'bold',
      boxShadow: '0px 0px 1px rgba(0, 0, 0, 1)',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
    }}>
      {cardNoMap[cardNo].type === 'パートナー' || cardNoMap[cardNo].type === '事件' ? <CheckIcon /> : toRoman[count]}
    </div>
  );
};
