export const ExpndButton = ({ handleClick, width, isOpened }) => {
  const bgColor = '#ffffff'
  return (
    <Button
      aria-label="delete"
      color="primary"
      onClick={handleClick}
      sx={{
        color: '#ffffff',
        bgcolor: darken(bgColor, 0.5),
        boxShadow: 1,
        width: width ?? 36, // 幅を設定します
        minWidth: width ?? 36,
        height: width ?? 36, // 高さを設定します
        '&:hover': {
          bgcolor: darken(bgColor, 0.4),
          boxShadow: 3,
        }
      }}
    >
      <ExpandMoreIcon
        sx={{
          transform: isOpened ? 'rotate(180deg)' : 'rotate(0deg)',
          transition: 'transform 0.3s',
        }}
      />
    </Button>
  )
};


