export const LinkButton = ({
  text, url, icon,
  color = '#ffffff',
  // bgcolor = lighten(mainBackgroundColor, 0.15),
}) => {

  const iconStyle = {
    color,
    fontSize: '24px' // アイコンサイズ
  };

  if (text) {
    return (
      <Button
        onClick={() => window.open(url, '_blank')}
        variant="outlined"
        // size="small"
        startIcon={React.cloneElement(icon, { style: iconStyle })}
      // sx={{
      //   color,
      //   bgcolor: darken('#ffffff',0.5),
      //   '&:hover': {
      //     bgcolor: darken('#ffffff',0.4),
      //   },
      // }}
      >
        {text}
      </Button>
    )
  }

  return (
    <IconButton
      onClick={() => window.open(url, '_blank')}
      size='small'
      sx={{
        color: '#1DA1F2',
        // bgcolor: lighten(mainBackgroundColor, 0.15),
        // '&:hover': {
        //   bgcolor: lighten(mainBackgroundColor, 0.2),
        // },
        margin: '2px',
      }}
    >
      {React.cloneElement(icon, { style: iconStyle })}
    </IconButton>
  )
}


export const ToggleStyledButton = ({
  onClick, isOn, width, height,
  onBgColor = darken('#ffffff', 0.35),
  offBgColor = darken('#ffffff', 0.7),
  children
}) => {

  return (
    <Button
      aria-label="ToggleStyledButton"
      variant='contained'
      onClick={onClick}
      sx={{
        width: width ?? '100%',
        minWidth: width ?? '100%',
        height: height ?? 20,
        color: isOn ? '#000000' : '#ffffff',
        backgroundColor: isOn ? onBgColor : offBgColor,
        '&:hover': {
          backgroundColor: isOn ? lighten(onBgColor, 0.2) : lighten(offBgColor, 0.2),
        },
        whiteSpace: 'nowrap', // 追加：文字列が改行しないようにする
        padding: '0 10px', // 追加：文字列の前後のスペースを調整（ここでは左右に10pxのスペースを設定）
      }}
    >
      {children}
    </Button>
  )
};


export const SortOrderButton = ({ handleClick, sortOrder }) => {

  return (
    <IconButton
      onClick={handleClick}
      sx={{
        width: '36px',
        height: '36px',
        color: '#000000',
        bgcolor: darken('#ffffff', 0.2),
        '&:hover': {
          bgcolor: darken('#ffffff', 0.15),
        }
      }}
    >
      {sortOrder === 'asc' ? '▲' : '▼'}
    </IconButton>

  )
}






