const getCardSize = (theme, windowWidth) => {


  const spacing = parseInt(theme.spacing(1), 10); // Gridのspacing
  const padding = parseInt(theme.spacing(1), 10); // padding
  const minWidth = 100; // カードの最小幅(カードの短編)

  // カードが配置される実際のスペースを計算
  const actualSpace = Math.floor(windowWidth) - padding;

  // 1行に表示できるカードの最大数を計算
  const maxCardsPerRow = Math.floor(actualSpace / minWidth);

  const totalSpacing = spacing * maxCardsPerRow

  // カード1枚あたりのスペースを計算
  return Math.floor((actualSpace - totalSpacing) / (maxCardsPerRow));

}








export const CardTop = ({actualHeight}) => {


  const [sortField, setSortField] = useRecoilState(atoms.sortField);
  const [sortOrder, setSortOrder] = useRecoilState(atoms.sortOrder);
  const [filterText, setFilterText] = useRecoilState(atoms.filterText);
  const [filterProduct, setFilterProduct] = useRecoilState(atoms.filterProduct);
  const [filterColor, setFilterColor] = useRecoilState(atoms.filterColor);
  const [filterType, setFilterType] = useRecoilState(atoms.filterType);
  const [filterRarity, setFilterRarity] = useRecoilState(atoms.filterRarity);
  const [filterParallel, setFilterParallel] = useRecoilState(atoms.filterParallel);
  const [hoveredCardNoAndSide, setHoveredCardNoAndSide] = useRecoilState(hoveredCardNoAndSideState);//要修正：CardTopが更新されてしまう

  const resetState = () => {
    setSortField(initialState.sortField);
    setSortOrder(initialState.sortOrder);
    setFilterText(initialState.filterText);
    setFilterProduct(initialState.filterProduct);
    setFilterColor(initialState.filterColor);
    setFilterType(initialState.filterType);
    setFilterRarity(initialState.filterRarity);
    setFilterParallel(initialState.filterParallel);
  };

  const displayedCardNoList = useMemo(() => {

    const filteredCardNoList = cardNoList.filter(cardNo => {
      const card = cardNoMap[cardNo]
      if (!card.name) return false;

      if (filterText
        && card.number !== filterText
        && card.id !== filterText
        && !card.name.includes(filterText)
        && !card.abilities.some(ability => ability.includes(filterText))
        && !card.cutIn.includes(filterText)
        && !card.inspiration.includes(filterText)
      ) {
        return false;
      }

      if (filterProduct && card.product !== filterProduct) return false;
      if (filterColor.length > 0 && !filterColor.some(color => card.colors.includes(color))) return false;
      if (filterType.length > 0 && !filterType.includes(card.type)) return false;

      const cardRarity = card.rarity.endsWith('P') ? card.rarity.slice(0, -1) : card.rarity
      if (card.rarity.endsWith('P')) {
        if (filterParallel.length > 0 && !filterParallel.includes('P(パラレル)')) {
          return false;
        }
      } else {
        if (filterParallel.length > 0 && !filterParallel.includes('(無印)')) {
          return false;
        }
      }
      if (filterRarity.length > 0 && !filterRarity.includes(cardRarity)) {
        return false;
      }


      return true;
    });








    const sortedCardNoList = [...filteredCardNoList].sort((a, b) => {
      if (cardNoMap[a][sortField] < cardNoMap[b][sortField]) return sortOrder === 'asc' ? -1 : 1;
      if (cardNoMap[a][sortField] > cardNoMap[b][sortField]) return sortOrder === 'asc' ? 1 : -1;
      return 0;
    });

    return sortedCardNoList;
  }, [sortField, sortOrder, filterText, filterColor, filterType, filterRarity, filterProduct, filterParallel]);








  const theme = useTheme();
  const [windowWidth, windowHeight] = useWindowSize()
  const [isMobile, setIsMobile] = useRecoilState(isMobileState);
  useEffect(() => {
    setIsMobile(windowHeight > windowWidth);
  }, [windowWidth, windowHeight]);

  const cardSize = getCardSize(theme, windowWidth)


  const [isAppBarOpen, setIsAppBarOpen] = useState(true);
  const [elementHeight, setElementHeight] = useState(0);

  const elementRef = React.useRef(null);


  React.useEffect(() => {
    const resizeObserver = new ResizeObserver(entries => {
      for (let entry of entries) {
        setElementHeight(entry.contentRect.height);
      }
    });

    if (elementRef.current) {
      resizeObserver.observe(elementRef.current);
    }

    return () => {
      if (elementRef.current) {
        resizeObserver.unobserve(elementRef.current);
      }
    };
  }, [isMobile]); // 依存配列にisMobileを追加

  // useEffect(() => {
  //   console.log(actualHeight, elementHeight)
  // }, [elementHeight]);
//  console.log(actualHeight, elementHeight)


  const [open, setOpen] = useState(false);

  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);






  if (!isMobile) {

    return (
      <div style={{
        display: 'flex',
        height: actualHeight,
      }}>
        <div style={{ flex: 1, overflowY: 'auto' }}>
          <DeckEditing
            props={{
              cardSize,
              viewWidth: windowWidth / 2,
            }}
          />
          {/* {windowHeight}, {actualHeight},{elementHeight} */}
        </div>

        <div style={{ flex: 1 }}>
          <div ref={elementRef}>
            <div style={{ display: 'flex', justifyContent: "flex-end", gap: 4, marginLeft: 8, marginRight: 8 , marginBottom:8 }}>
              <div style={{ display: 'flex', justifyContent: 'center', width: '100%' }}>
                <ButtonBase
                  onClick={() => setIsAppBarOpen(!isAppBarOpen)}
                  style={{ flexGrow: 1 }} // この行を追加
                >
                  <SearchIcon />
                  <ExpandMoreIcon
                    sx={{
                      transform: isAppBarOpen ? 'rotate(180deg)' : 'rotate(0deg)',
                      transition: 'transform 0.3s',
                    }}
                  />
                </ButtonBase>
              </div>
              <RefreshButton handleClick={resetState} />
            </div>
            <Collapse in={isAppBarOpen}>
              <SortAndFilter props={{ isAppBarOpen, setIsAppBarOpen }} />
            </Collapse>
          </div>

          <div style={{ height: actualHeight - elementHeight , overflowY: 'auto' }}>
            <CardOfList
              props={{
                displayedCardNoList,
                viewWidth: windowWidth / 2,
              }}
            />
          </div>
        </div>
        {hoveredCardNoAndSide[0] &&
          <HoverCard cardNo={hoveredCardNoAndSide[0]} side={hoveredCardNoAndSide[1]} />
        }

      </div>
    );
  }





  return (
    <div>
      <HideOnScrollAppBar>
        <DeckEditing
          props={{
            cardSize,
            viewWidth: windowWidth,
            isMobile,
          }}
        />
      </HideOnScrollAppBar>

      <Modal
        open={open}
        onClose={handleClose}
      >
        <div
          style={{ backgroundColor: '#000000' }}
        >
          <SortAndFilter
          />
        </div>
      </Modal>

      <Fab color="primary" aria-label="add" onClick={handleOpen}
        style={{ position: 'fixed', bottom: 16, right: 16 }}>
        <SearchIcon style={{ color: '#ffffff', fontSize: 32 }} />
      </Fab>

      <CardOfList
        props={{
          displayedCardNoList,
          viewWidth: windowWidth,
          isMobile,
        }}
      />
      <div style={{ height: 100 }}>

      </div>

    </div>
  );
};


