import React, { useState } from 'react';
import { useRecoilState } from 'recoil';
import _ from 'lodash';
import { hoveredCardNoAndSideState } from '../states/recoilStates';
import { Unstable_Grid2 as Grid } from '@mui/material';
import { cardNoMap } from '../../constants/cards';
import { isPartnerOrCase3, isPartnerOrCase2 } from '../../utils/commonUtils';
import { CardDetails } from './CardDetails';
import { DeckChart } from './Chart';
import { CardsPerLevel } from './CardsPerLevel';
import { OperableCard } from '../../components/OperableCard';
import { getCardWidth, getCardHeight, getCardBorderRadius } from '../../utils/commonUtils';
import { cardNoList, cardList, productData } from '../../constants/cards';
import { CardImg2 } from '../../components/Img';
import { useWindowSize } from '../../hooks/commonHooks';
import { HoverCard } from '../../components/HoverCard';
import { removeAndSetCardSet } from '../utils/updateCardSet';








const textToKey = {
  'パートナー': 'partner',
  '事件': 'case',
}

const keyToText = {
   'partner':'パートナー',
    'case':'事件',
}


export const CardsOfCardSet = ({ cardSet, setCardSet, viewWidth, isMobile , editable = true }) => {
  const length = cardSet.deck.length;
  const cardSize = viewWidth / 8 * 1.4;



  //カードを長押しすると拡大
  const [selectedCardNo, setSelectedCardNo] = useState('');
  const [open, setOpen] = useState(false);




  const handleClick = (cardNo) => {

    setHoveredCardNoAndSide(['', '']);
    removeAndSetCardSet(setCardSet,cardNo)



  };

  const [hoveredCardNoAndSide, setHoveredCardNoAndSide] = useRecoilState(hoveredCardNoAndSideState);



  return (<>
    <Grid container spacing={0.5} sx={{ paddingX: 1, paddingTop: 0.5 }}>
      <Grid>
        {cardSet.partner
          ? <OperableCard
            cardNo={cardSet.partner}
            cardSize={cardSize}
            handleClick={() => handleClick(cardSet.partner)}
            handleHoverOn={isMobile ? undefined : () => setHoveredCardNoAndSide([cardSet.partner, 'right'])}
            handleHoverOff={isMobile ? undefined : () => setHoveredCardNoAndSide(['', ''])}
            handleLongPress={() => {
              setSelectedCardNo(cardSet.partner);
              setOpen(true);
            }}
            editable={editable}
            isCountDisplay={false}
            />
          : <EmptyCard
            cardSize={cardSize / 1.4}
            type={'partner'} />}
      </Grid>
      <Grid>
        {cardSet.case
          ? <OperableCard
            cardNo={cardSet.case}
            cardSize={cardSize * 1.4 * 1.4}
            handleClick={() => handleClick(cardSet.case)}
            handleHoverOn={isMobile ? undefined : () => setHoveredCardNoAndSide([cardSet.case, 'right'])}
            handleHoverOff={isMobile ? undefined : () => setHoveredCardNoAndSide(['', ''])}
            handleLongPress={() => {
              setSelectedCardNo(cardSet.case);
              setOpen(true);
            }} 
            editable={editable}
            isCountDisplay={false}
            />
          : <EmptyCard
            cardSize={cardSize}
            type={'case'} />}
      </Grid>
      {(!isMobile || !editable) &&
        <Grid>
          <DeckChart deck={cardSet.deck} />
        </Grid>}
    </Grid>
    <CardsPerLevel
      cardNos={cardSet.deck}
      viewWidth={viewWidth}
      handleClick={handleClick}
      setSelectedCardNo={setSelectedCardNo}
      setOpen={setOpen}
      isMobile={isMobile}
      editable={editable}

      />

    {selectedCardNo && //関数で渡すので必要
      <CardDetails
        open={open}
        cardNoList={isPartnerOrCase3(selectedCardNo)
          ? [selectedCardNo]
          : Array.from(new Set(cardSet.deck)) // 重複分を除く
        }
        selectedCardNo={selectedCardNo}
        setSelectedCardNo={setSelectedCardNo}
        handleClose={() => setOpen(false)} />}



  </>);
};

const EmptyCard = ({  type,cardSize }) => {

  return (
    <div>
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          border: '1px solid gray',
          borderRadius: getCardBorderRadius(cardSize),
          width: getCardWidth(cardSize, type),
          height: getCardHeight(cardSize, type),
          fontWeight: 'bold',
          textTransform: 'uppercase',
          fontSize:cardSize/6,
        }}
      >
        {type
          ? <div>{keyToText[type]}</div>
          : <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
            <div>character</div>
            <div>or</div>
            <div>event</div>
          </div>
        }
      </div>
    </div>
  )
};

