import { cardNoList, cardNoMap, cardList, productData } from '../constants/cards';


export const isPartnerOrCase = (card) => {
  return card.type === 'partner' || card.type === 'case';
}

export const isPartnerOrCase2 = (type) => {
  return type === 'パートナー' || type === '事件';
}

export const isPartnerOrCase3 = (cardNo) => {
  return cardNoMap[cardNo].type === 'パートナー' || cardNoMap[cardNo].type === '事件';
}





export const getCardWidth = (size, type) => {
  return  type === 'case' ? size * 1.4 : size;
}
export const getCardHeight = (size, type) => {
  return  type === 'case' ? size : size * 1.4;
}
export const getCardBorderRadius = (size) =>{
  return size * 1.4 / 25;
}
