import React from 'react';

import { cardNoList, cardNoMap , cardList, productData } from '../constants/cards';


export const Img = ({ name, size, borderRadius }) => {

  return (
    <img
      src={name
        ? `${process.env.PUBLIC_URL}/${name}.png`
        : 'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7'
      }
      alt=''
      style={{
        width: size,
        height: size,
        borderRadius: borderRadius || 0,
        verticalAlign: 'bottom',

      }}
    />
  );
};

export const CardImg = ({ card, size, boxShadow }) => {
  let imgSrc;
  if (card.isBack) {
    imgSrc = `${process.env.PUBLIC_URL}/back.png`;
  } else if (card.number) {
    imgSrc = `${process.env.PUBLIC_URL}/${card.number}.jpg`;
  } else {
    imgSrc = 'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7';
  }

  return (
    <img
      src={imgSrc}
      alt=''
      style={{
        width: card.type === 'case' ? size : size / 1.4,
        height: card.type === 'case' ? size / 1.4 : size,
        borderRadius: `${size / 25}px`,
        verticalAlign: 'bottom',
        boxShadow,
      }}
    />
  );
};

export const CardImg2 = ({ cardNo, size, boxShadow, fade = false }) => {
  const card = cardNoMap[cardNo]
  let imgSrc;
  if (card.isBack) {
    imgSrc = `${process.env.PUBLIC_URL}/back.png`;
  } else if (card.number) {
    imgSrc = `${process.env.PUBLIC_URL}/${card.number}.jpg`;
  } else {
    imgSrc = 'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7';
  }

  const [loaded, setLoaded] = React.useState(false);

  const width = size/1.4
  const height = card.type === '事件' ? size / 1.4 / 1.4 : size

  return (
    <img
      src={imgSrc}
      alt=''
      onLoad={() => setLoaded(true)}
      style={{
        width,
        height,
        borderRadius: `${ Math.max(width,height) / 25}px`,
        verticalAlign: 'bottom',
        boxShadow,
        opacity: loaded || !fade ? 1 : 0,
        transition: fade ? 'opacity 0.5s' : undefined
      }}
    />
  );
};
