import React, { useState } from 'react';
import { useRecoilState } from 'recoil';

import _ from 'lodash';
import { Alert, Unstable_Grid2 as Grid, Snackbar } from '@mui/material';
import { darken, lighten } from '@mui/system';
import ErrorOutlineIcon from '@mui/icons-material/ErrorOutline'


import { cardSetState, isMobileState } from '../states/recoilStates';



import { cardNoList, cardNoMap, cardList, productData } from '../../constants/cards';


import { CardDetails } from './CardDetails';
import { isPartnerOrCase } from '../../utils/commonUtils';
import { useWindowSize } from '../../hooks/commonHooks';
import { HoverCard } from '../../components/HoverCard';
import { OperableCard } from '../../components/OperableCard';
import { updateCardSetWithNewCard } from '../utils/updateCardSet';
import { CustomSnackBar } from '../../components/CustomSnackBar';
import { calculateCount } from './CountDisplay';


export const CardOfList = ({ props }) => {
  const { displayedCardNoList, viewWidth } = props
  const [cardSet, setCardSet] = useRecoilState(cardSetState);
  const [selectedCardNo, setSelectedCardNo] = useState('');
  const [open, setOpen] = useState(false);
  const [hoveredCardNo, setHoveredCardNo] = useState('');
  const [isMobile, setIsMobile] = useRecoilState(isMobileState);


  //SnackBar
  const [message, setMessage] = useState('');
  const [openSnackbar, setOpenSnackbar] = useState(false);
  const showMessage = (msg) => {
    setMessage(msg);
    setOpenSnackbar(true);
  };
  const handleCloseSnackBar = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpenSnackbar(false);
  };

  const cardSize = isMobile
    ? Math.floor((viewWidth - 8 * 2) / 4 * 1.4)
    : Math.floor((viewWidth - 8 * 2) / 8 * 1.4)

  return (
    <>
      <Grid container sx={{ padding: 1 }}>
        {displayedCardNoList.map(cardNo => {
          // cardSet.deckに含まれるcardNoの数をカウント
          // let quantityIncludedInDeck = 0;
          const count = calculateCount({cardNo, cardSet});
          // if (cardNoMap[cardNo].type === 'パートナー' && cardNo === cardSet.partner) {
          //   count = 1
          // } else if (cardNoMap[cardNo].type === '事件' && cardNo === cardSet.case) {
          //   count = 1
          // } else {
          //   quantityIncludedInDeck = cardSet.deck.filter(c => c === cardNo).length
          //   count = quantityIncludedInDeck
          // }



          return (
            <Grid key={cardNo}>
              <OperableCard
                cardNo={cardNo}
                count={count} // カウントを渡す
                cardSize={cardSize}
                handleHoverOn={isMobile ? undefined : () => setHoveredCardNo(cardNo)}
                handleHoverOff={isMobile ? undefined : () => setHoveredCardNo('')}
                handleLongPress={() => {
                  setSelectedCardNo(cardNo)
                  setOpen(true)
                }}
                handleClick={() => {
                  setHoveredCardNo(null);
                  updateCardSetWithNewCard({
                    cardNo,
                    cardSet,
                    setCardSet,
                    showMessage,
                  });
                }}
                editable={true}
              />
              {hoveredCardNo === cardNo &&
                <HoverCard cardNo={cardNo} side={'left'} />
              }
            </Grid>
          )
        })}
      </Grid>

      {selectedCardNo &&
        <CardDetails
          open={open}
          cardNoList={displayedCardNoList}
          selectedCardNo={selectedCardNo}
          setSelectedCardNo={setSelectedCardNo}
          handleClose={() => setOpen(false)}
        />
      }

      <CustomSnackBar
        openSnackbar={openSnackbar}
        handleClose={handleCloseSnackBar}
        message={message}
      />
    </>
  );
};


