import React, { useState } from 'react';
import _ from 'lodash';

import { ButtonBase, Unstable_Grid2 as Grid } from '@mui/material';

import { cardNoList, cardNoMap, cardList, productData } from '../constants/cards';


import { CardImg2 } from '../components/Img';
import { useWindowSize } from '../hooks/commonHooks';



export const HoverCard = ({ cardNo, side }) => {
  const [windowWidth, windowHeight] = useWindowSize();
  const displayableWidth = windowWidth / 2 - 16
  const displayableHeight = windowHeight * 0.8

  let cardWidth = Math.min(displayableWidth, 1000 / 1.4)
  let cardHeight = Math.min(displayableHeight, 1000)
  if (displayableWidth > (displayableHeight / 1.4)) {
    cardWidth = displayableHeight / 1.4
  } else {
    cardHeight = displayableWidth * 1.4
  }



  return (
    <div style={{
      position: 'absolute',
      top: '50%', // 画面の中央に配置
      left: side === 'left' ? '25%' : '75%',
      width: cardWidth,
      height: cardHeight,
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      transform: 'translate(-50%, -50%)', // 自身の高さと幅の半分だけ上と左にずらす
    }}>
      <CardImg2 cardNo={cardNo} size={cardHeight} />
    </div>
  )
}
